body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-body {
    background-color: #262626;
    position: -webkit-sticky;
    position: sticky;
    z-index: 1;
    top: 0;
}
.nav-body li {
    list-style: none;
}
.nav-body a {
    color: white;
    text-decoration: none;
}
.navbar {
    min-height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 24px;
}
.nav-logo {
    font-size: 2rem;
    text-transform: uppercase;
}
.nav-logo img {
    width: auto;
    height: 55px;
}
.nav-menu {
    display: flex;
	justify-content: space-between;
	align-items: center;
	grid-gap: 60px;
	gap: 60px;
}
.nav-link {
    transition: 0.6s;
}
.nav-link:hover {
    color: greenyellow;
}
.hamburger {
    display: none;
    cursor: pointer;
}
.bar{
	display: block;
	width: 25px;
	height: 3px;
	margin: 5px auto;
	transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	background-color: white;
}
@media (max-width: 768px) {
    .hamburger {
        display: block;
    }
    .hamburger.active .bar:nth-child(2) {
        opacity: 0;
    }
    .hamburger.active .bar:nth-child(1) {
        -webkit-transform: translateY(8px) rotate(45deg);
                transform: translateY(8px) rotate(45deg);
    }
    .hamburger.active .bar:nth-child(3) {
        -webkit-transform: translateY(-8px) rotate(-45deg);
                transform: translateY(-8px) rotate(-45deg);
    }
    .nav-menu {
        position: fixed;
		left: -100%;
		top: 53px;
		grid-gap: 5;
		gap: 5;
		flex-direction: column;
		background-color: #262626;
		width: 100%;
		text-align: center;
		transition: 0.7s;
        padding: 15px;
    }
    .nav-menu.active {
        left: 0;
    }
}
img {
	width:  200px;
	height: 100%;
}
.price {
	font-weight: bold;
	font-size: 2rem;
	color: orange;
}
.OneButter h2{
	min-height: 75px;
	max-width: 250px;
	margin: 1em;
}
.description{
	min-height: 100px;
	width: 300px;
	max-width: 300px;
	text-align: center;
}
.ownStyle{
    min-width: 350px;
    text-align: center;
}
@media (max-width: 600px) {
    .ownStyle{
        min-width: 240px;
    }
}
